import {
    UI_OPEN_SIDEBAR,
    UI_CLOSE_SIDEBAR
} from '../actionTypes'

const initState = {
    sidebarOpen: true
}

const uiReducer = (state=initState, action) => {
    switch(action.type) {
        case UI_OPEN_SIDEBAR: {
            return {
                ...state,
                sidebarOpen: true
            }
        }

        case UI_CLOSE_SIDEBAR: {
            return {
                ...state,
                sidebarOpen: false
            }
        }

        default:
            return state
    }
}

export default uiReducer